@import url(https://fonts.googleapis.com/css2?family=Anton&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App {
  margin: 30px auto;
  width: 100%;
  background-color: #ffffff;
}
.Heading {
  font-family: "Helvetica";
  text-transform: uppercase;
  text-align: center;
  font-size: 40px;
  letter-spacing: 3px;
}

.more {
  padding: 10px;
  text-align: center;
}

.Cards {
  display: flex;
  justify-content: space-evenly;
  margin: 60px;
  flex-wrap: wrap;
}

.Card {
  min-width: 200px;
  width: 300px;
  height: 200px;
  border: 1px solid #f3f3f3;
  box-shadow: 2px 2px #f3f3f3;
  padding: 20px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  margin: 10px;
}
.heading {
  color: #a7a7a7;
  font-size: 18px;
}
.value {
  font-size: 30px;
  letter-spacing: 1px;
}
.recovered {
  border-top: 5px solid #60bea0;
}
.confirmed {
  border-top: 5px solid #0a7ea2;
}
.deaths {
  border-top: 5px solid #db565d;
}
.lastupdate {
  color: #a7a7a7;
}

.SearchBar {
  display: flex;
  justify-content: center;
  margin: 50px;
}

.Countries {
  border: none;
  border-bottom: 2px solid #cccccc;
  padding: 10px;
  font-size: 20px;
  box-sizing: border-box;
  outline: none;
  width: 250px;
  transition: all 0.3s ease;
  transform-origin: center;
}
.Countries:focus {
  border-bottom: 3px solid #121212;
}

.Map {
  height: 600px;
  background-color: #fff;
  padding: 16px;
  border-radius: 20px;
  margin-top: 20px;
  border: 2px solid #f3f3f3;
  width: 80%;
  margin: auto;
}

.Map .leaflet-container {
  height: 100%;
}
.popup-container {
  width: 150px;
}
.popup-flag {
  height: 90px;
  width: 90%;
  margin: 10px auto;
  background-size: cover;
  background-position: center;
  border-radius: 5px;
}
.popup-country {
  text-align: center;
  font-size: 20px;
  color: #121212;
}
.popup-cases,
.popup-recovered,
.popup-deaths {
  font-size: 13px;
  margin-top: 5px;
}

@media only screen and (max-width: 500px) {
  .Map {
    height: 50vh;
    width: 100vw;
  }
}

.Map .leaflet-popup-content-wrapper,
.Map .leaflet-popup-tip {
  background-color: #f5f5f5;
}


.chartHeading {
  text-align: center;
  font-size: 40px;
  color: #a7a7a7;
  font-weight: 600;
  margin-top: 80px;
}
.charts {
  display: flex;
  justify-content: center;
  margin: 50px auto;
  border: none;
  border-bottom: 2px solid #cccccc;
  padding: 10px;
  font-size: 20px;
  outline: none;
  color: #121212;
}
.charts:focus {
  border-bottom: 3px solid #121212;
}

